<script setup>
import Button from './Button.vue'
import { ref } from 'vue'

const videoRef = ref(null)
const isShowStartBtn = ref(true)
const isShowChoose = ref(false)
const emit = defineEmits(['nextVideo'])

function playVideo() {
	isShowStartBtn.value = false
	videoRef.value.play()
}

function showChoose() {
	isShowChoose.value = true
}

function nextVideo(num) {
	emit('nextVideo', num)
}
</script>

<template>
	<div class="container">
		<div class="container__video">
			<div class="video">
				<video
					@ended="showChoose"
					ref="videoRef"
					src="../video/main_video.mp4"
				></video>
			</div>
		</div>
		<div class="container__absolute">
			<Transition mode="out-in">
				<div v-if="isShowStartBtn" class="container__start_btn">
					<Button font-size="1.2em" @click="playVideo">Начать</Button>
				</div>
			</Transition>

			<Transition mode="out-in">
				<div v-if="isShowChoose" class="container__text">
					<div class="text">Как поступить работнику?</div>
				</div>
			</Transition>

			<Transition mode="out-in">
				<div v-if="isShowChoose" class="container__choose_btns">
					<div class="coose_bts">
						<Button @click="nextVideo(1)" font-size="0.8em"
							>Обернуться и узнать, что спрашивают</Button
						>
						<Button @click="nextVideo(2)" font-size="0.8em"
							>Отключить инструмент и узнать, что спрашивают</Button
						>
						<Button @click="nextVideo(3)" font-size="0.8em"
							>Обернуться с инструментом и узнать, что спрашивают</Button
						>
					</div>
				</div>
			</Transition>
		</div>
	</div>
</template>

<style scoped>
.container {
	position: relative;
}

.container__video,
.video,
video {
	max-width: 100vw;
	z-index: -1;
}

.container__absolute {
	position: absolute;
	top: 0;
	width: 100vw;
	height: 56vw;
}

.container__start_btn {
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	align-items: center;
	height: 100%;
}

.container__text {
	margin-top: 6.5vw;
	display: flex;
	justify-content: center;
}

.text {
	font-size: 1.5em;
	padding: 1vw 1.2vw;
	background-color: #333f50;
	font-weight: 900;
	color: var(--text-color);
	border-radius: 1vw;
	text-align: center;
}

.container__choose_btns {
	position: absolute;
	bottom: 2vw; /* Расстояние от нижнего края */
	width: 100%; /* Растянуть на всю ширину */
	display: flex;
	justify-content: center; /* Центрируем кнопки */
}

.coose_bts {
	margin: 1vw;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 2vw;
}

.v-enter-active,
.v-leave-active {
	transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
	opacity: 0;
}
</style>
