<script setup>
import { onMounted, ref } from 'vue'
import Button from './Button.vue'

const emit = defineEmits(['return'])

const props = defineProps({
	video: {
		type: String,
		required: true,
	},
	isCorrect: {
		type: Boolean,
		required: true,
	},
	textMsg: {
		type: String,
		required: true,
	},
})

const isShowPlayBtn = ref(false)

const videoRef = ref(null)

onMounted(() => {
	setTimeout(() => {
		if (videoRef.value.currentTime === 0) {
			isShowPlayBtn.value = true
		}
	}, 3000)
})

function playVideo() {
	isShowPlayBtn.value = false
	videoRef.value.play()
}

const isShowMsg = ref(false)
</script>

<template>
	<div class="container">
		<div class="container__video">
			<video
				@play="isShowPlayBtn = false"
				@ended="isShowMsg = true"
				autoplay="true"
				ref="videoRef"
				:src="video"
			></video>
		</div>

		<Transition mode="out-in">
			<div v-if="isShowPlayBtn" class="container__play_btn">
				<img
					@click="playVideo"
					src="../assets/play_circle_50dp_000000_FILL0_wght400_GRAD0_opsz48.svg"
					alt=""
				/>
			</div>
		</Transition>

		<Transition mode="out-in">
			<div v-if="isShowMsg" class="container__msg">
				<div
					class="msg"
					:class="[isCorrect ? 'green' : 'red']"
					v-html="textMsg"
				></div>
				<div class="img">
					<img v-if="!isCorrect" src="../assets/down.png" alt="" />
					<img v-else src="../assets/up.png" alt="" />
				</div>
				<div class="btn">
					<Button @click="$emit('return')" v-if="!isCorrect"
						>Это неверный вариант. Нажмите сюда, чтобы перейти к выбору</Button
					>
					<Button @click="$emit('return')" v-else>
						Нажмите сюда, чтобы вернуться и посмотреть, что будет, если выбрать
						другие варианты"
					</Button>
				</div>
			</div>
		</Transition>
	</div>
</template>

<style scoped>
.container {
	position: relative;
	animation: show 1s;
}

@keyframes show {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.container__video {
	z-index: -2;
}

.container__video > video {
	max-width: 100vw;
	z-index: -1;
}

.container__play_btn {
	top: 0;
	position: absolute;
	width: 100%;
	height: 100%;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: center;
}

.container__play_btn > img {
	width: 10vw;
	cursor: pointer;
	transition: scale 0.5s;
}

.container__play_btn > img:hover {
	scale: 1.1;
}

.container__msg {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	display: flex;
	flex-flow: column wrap;
	justify-content: space-evenly;
	align-items: center;
}

.msg {
	top: 10vw;
	color: var(--text-color);
	padding: 1vw;
	font-weight: 900;
	font-size: 1em;
	max-width: 80vw;
	text-align: center;
	border-radius: 1.4vw;
}

.btn {
	max-width: 80vw;
}

.img {
	display: flex;
	justify-content: flex-end;
	align-items: end;
	width: 80%;
}

.img img {
	width: 7vw;
}

.red {
	background-color: var(--red-color);
}

.green {
	background-color: var(--green-color);
}

.v-enter-active,
.v-leave-active {
	transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
	opacity: 0;
}
</style>
